// Comments

.comment-field {
    margin: 0;
    padding: 0;
    margin-bottom: 21px;
    @media (min-width: @tablet) {
      padding: 0;
      margin-bottom: 65px;
    }
    @media (min-width: @desktop) {
      margin-top: 49px;
      margin-bottom: 67px;
    }
  }

  .comment-field__textarea {
    margin: 0;
    padding: 13px 15px;
    width: 250px;
    height: 124px;
    font-family: 'Oswald', Arial, sans-serif;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    color: @specialdarkgrey;
    border: 1px solid @forminput;
    @media (min-width: @tablet) {
      width: 601px;
      height: 146px;
      font-size: 20px;
      line-height: 30px;
    }
    @media (min-width: @desktop) {
      width: 1190px;
      height: 147px;
    }
  }

  .comment-field__wrapper {
    padding: 0 20px;
    margin: 0 auto;

    @media (min-width:@tablet) {
      margin: 0 auto;
      width: 630px;
      padding: 0 70px;
    }

    @media (min-width: @desktop) {
      width: 1220px;
      padding: 0;
    }
  }
