
.page-header {
  @media(min-width:@desktop) {
  }

  &--form {
    background-color: @white;
  }
}

.page-header__wrapper {

  @media (min-width: @tablet) {

    width: 708px;
    margin: 0 auto;
    padding: 24px 30px 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

  }

  @media (min-width: @desktop) {
    width: 1220px;
    margin: 0 auto;
    padding: 53px 110px 22px;
    justify-content: space-between;
    
  }
}

.page-header__wrapper-upper {
  padding: 14px 20px;
  display: flex;
  justify-content: space-between;
  @media (min-width: @tablet) {
    padding: 0;
    align-items: center;
    width: 174px;
 }
}

.page-header__logo {

  
  @media (min-width: @tablet) {
    min-height: 51px;
    width: 174px;
    margin-bottom: 60px;
    &:hover {
      opacity:80% ;
    }
    &:active {
      opacity: 60%;
    }
  }

  /*

  @media (min-width: @desktop) {
    min-height: 51px;
    width: 50px;
    align-items: center;
  }
  */
}

.page-header__logo-text {
  padding-top: 10px;
  display:  block;

  @media (min-width: @tablet) {
    display: none;
    margin: 0;
    min-height: 51px;
    width: 104px;
  }

  @media (min-width: @desktop) {
    display: none;
    margin: 0;
    min-height: 23px;
    width: 118px;
  }
}

.page-header__logo-div {
  @media (min-width: @tablet) {
    display: none;
    width: 0;

  }
}

.page-header__icon {
  width: auto;
}