.filter-cat {
  @media (min-width:@tablet) {
    margin: 0 auto;
    width: 768px;
  }
  @media (min-width: @desktop) {
    width: 1440px;
    margin: 0 auto;
  }
}
.filter-cat__border {
  border: none;
  border-top: 2px solid @basicgreen;
  @media (min-width: @desktop) {
    border-top: 2px solid green;
  }
}

.filter-cat__container{
  @media (min-width:@tablet) {
    margin: 0 auto;
    width: 630px;
    padding: 0 70px;
  }

  @media (min-width: @desktop) {
    display: flex;
    justify-content: space-between;
    margin-bottom: 69px;
    width: 1220px;
    padding: 0;
  }
}

.filter-cat__list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.filter-cat__legend {
  padding: 0 20px;
  font-size: 24px;
  line-height: 26px;
  margin-bottom: 37px;

  @media (min-width: @tablet) {
    padding: 0 50px;
    margin-bottom: 76px;
    font-size: 36px;
    line-height: 36px;
  }

  @media (min-width: @desktop) {
    margin-bottom: 73px;
    padding:0 40px;
    margin-bottom: 71px;
    text-align: left;
  }

}

.filter-cat__label {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: @specialdarkgrey;
  margin-right: 14px;
  text-align: justify;
  white-space: nowrap;
  position: relative;

  @media (min-width: @tablet) {
    font-size: 20px;
    line-height: 30px;
  }
}

.filter-cat__text-input {
  margin-bottom: 13px;
  padding: 13px 0 13px 15px;
  font-family: 'Oswald', Arial, sans-serif;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: @specialdarkgrey;
  border: 1px solid @forminput;
  width: 100%;
  margin: 0;

  &:hover {
    border: 1px solid @specialgrey;
  }

  &:focus {
    border: 1px solid @black;
    outline: none;
    width: 100%;
  }
}

.filter-cat__text-input {
  @media (min-width: @tablet) {
    margin-left: 23px;
    padding: 11px 19px;
    font-size: 20px;
    line-height: 30px;
  }
  @media (min-width: @desktop) {
    margin-left: 13px;
    padding: 11px 19px 10px;
    &--age {
      margin-bottom: 0;
    }
  }
}

.filter-cat__text-input:disabled {
 border: 2px solid rgba(68, 68, 68, 1);
}

.filter-cat__text-input:invalid {
  border: 2px solid #FF8282;
}

.filter-cat__extra-wrapper {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: baseline;
  padding-bottom: 13px;

  @media (min-width: @tablet) {
    padding-bottom: 17px;
  }

  @media (min-width:@desktop){
    padding-bottom: 20px;
    width: 569px;
    &:last-child{
      padding-bottom: 0;
    }
     &--contacts {
      width: 570px;
    }
  }

}



//   

.filter-input-radio+label::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  left: -45px;
  top: -2px;
  border: 1px solid @specialgrey;
  border-radius: 50%;
  &:disabled {
    opacity: 30%;
  }

  @media(min-width: @tablet) {
    width: 40px;
    height: 40px;
    left: -60px;
    top: -6px;
  }

}
.filter-input-radio:checked + label::after {
  content: "";
  position: absolute;
  width: 14px;
  height: 14px;
  left: -36px;
  top: 7px;
  background-color: @basicgreen;
  border-radius: 50%;
  &:disabled {
    opacity: 30%;
  }
  @media(min-width: @tablet) {
    width: 18px;
    height: 18px;
    left: -48px;
    top: 6px;
  }

}
.filter-input-radio:disabled + label {
  opacity: 30%;

}

// 
.filter-input-checkbox+label::after {
  display: block;
  content: "";
  position: absolute;
  left: -35px;
  top: -3px;
  width: 30px;
  height: 30px;
  left: -46px;
  top: -2px;
  border: 1px solid @specialgrey;
  @media(min-width: @tablet) {
    width: 40px;
    height: 40px;
    left: -59px;
    top: -5px;
  }
}

.filter-input-checkbox:checked+label::before {
  content: "";
  display: block;
  position: absolute;
  left: -38px;
  top: 7px;
  width: 18px;
  height: 14px;
  background-image: url("../../img/svg/Vector.svg");
  background-repeat: no-repeat;
  @media(min-width: @tablet) {
    top: 9px;
    left: -46px;
  }
}

.filter-input-checkbox:disabled+label {
  opacity: 30%;
}
