.catalog {
  border-top: 1px solid @specialgreylight;

  @media (min-width: @tablet) {
    margin-bottom: 68px;
    border: none;
  }
  @media (min-width: @desktop) {
    margin-bottom: 76px;
  }
}

.catalog__wrapper {
  @media (min-width: @tablet) {
    width: 708px;
    margin: 0 auto;
  }
  @media (min-width: @desktop) {
    width: 1220px;
    margin: 0 auto;
  }
}

.catalog__title-main {
  padding: 0 20px;
  margin: 27px 0 40px;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  @media (min-width: @tablet) {
    margin-top: 0;
    margin-bottom: 164px;
    padding: 0;
    font-size: 60px;
    line-height: 60px;
  }
  @media (min-width: @desktop) {
    margin-bottom: 168px;
  }
}

.catalog__list {
  padding: 0;
  margin: 0;
  list-style: none;
  @media (min-width: @tablet) {
    display: grid;
    grid-template-columns: 324px 324px;
    grid-column-gap: 60px;
    grid-row-gap: 141px;
  }
  @media (min-width: @desktop) {
    grid-template-columns: 245px 245px 245px 245px;
    grid-column-gap: 80px;
    grid-row-gap: 141px;
  }
}
