.item-catalog__wrapper-main{
  margin-bottom: 25px;
  padding: 0 20px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  height: 100%;
  
  @media (min-width: @tablet) {
    padding: 0 37px 0px 39px;
    margin: 0;
    position: relative;
    flex-direction: column;
    background-color: @basicgrey;
  }
  @media (min-width: @desktop) {
    padding: 0 30px 0 35px;
  }
}


.item-catalog {
  border-top: 1px solid @specialgreylight;
}


.item-catalog__title {
  width: 76px;
  margin-top: 20px;
  margin-bottom: 14px;
  align-self: flex-start;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  color: @specialsuperdark;
  @media (min-width: @tablet) {
    margin: 30px 0 0 39px;
    padding-left: 10px;
    margin-top: 120px;
    padding-top: 41px;
    width: 145px;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
  }
  @media (min-width: @desktop) {
    margin: 0 0 0 18px;
    margin-top: 120px;
  }
}

.item-catalog__table {
  width: 142px;
  margin-bottom: 13px;
  font-family: Arial, sans-serif;
  font-size: 12px;
  line-height: 16px;
  color: @specialdarkgrey;
  @media (min-width: @tablet) {
    padding-top: 14px;
    width: 248px;
    font-size: 16px;
    line-height: 16px;
  }
  @media (min-width: @desktop) {
    width: 180px;
  }
}

.item-catalog__border {
  width: 145px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  @media (min-width: @tablet) {
    width: 250px;
    border-bottom: 1px solid @specialgrey;
    padding-top: 7px;
    padding-bottom: 7px;
  }
  @media (min-width: @desktop) {
    width: 180px;
  }
}

.item-catalog__img {
  display: flex;
  align-self: center;

  @media (min-width: @tablet) {
    padding: 0;
  }
}

.item-catalog__pic {
  padding: 0 20px;
  width: auto;
  @media (min-width: @tablet) {
    position: absolute;
    left: 20%;
    bottom: 69%;
  }

  @media (min-width: @desktop) {
    padding: 0;
    left: 35;
    bottom: 73%;
  }
}

