//Program
.program-field__list {

    @media (min-width: @tablet) {
      padding-bottom: 23px;
   }
    @media (min-width: @desktop) {
      width: 568px;
      padding-top: 30px;
      }
    }


   .program-field {
    padding-top: 26px;
    padding-bottom: 0;
    margin: 0 0 24px;
    border: none;
    border-bottom: 1px solid @formtable;
    @media (min-width: @tablet) {
      padding: 0;
      padding-top: 3px;
      padding-bottom: 4px;
      margin: 0 0 75px;
      width: 630px;
      border: 1px solid @forminput;
    }
    @media (min-width: @desktop) {
      padding: 0;
      margin: 0 auto;
      width: 568px;
      border: 1px solid @forminput;
    }
  }

  .program-field__item {
    padding-bottom: 26px;
    padding-left: 52px;
    font-size: 16px;
    line-height: 18px;
    text-transform: uppercase;
    color: @specialsuperdark;
    @media (min-width: @tablet) {
      padding: 25px 0 0 87px;
      font-size: 20px;
      line-height: 30px;
    }
    @media (min-width: @desktop) {
      padding: 0 0 23px 88px;
      font-size: 20px;
      line-height: 30px;

      &--advice {
        padding-bottom: 0;
      }
    }
  }
