.social__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  width: 130px;
  align-self: center;
  justify-content: space-between;
  @media (min-width: @tablet) {
    flex-direction: row;
  }
}

.social__item {
  display: inline-block;
  align-self: center;

}

.social__link {
  text-decoration: none;
  @media (min-width: @tablet) {
    display: block;
    width: 38px;
      height: 25px;

    &--instagram {
      width: 23px;
      height: 23px;
    }
    &--facebook {
      width: 27px;
      height: 27px;
    }
  }

}

.social__icon {
  width: 100%;
  height: auto;
  stroke-width: 1.1%;
  stroke: #666666;
  @media (min-width: @tablet) {
    stroke-width: 0.1%;
    stroke: rgba(102, 102, 102, 1);
  }

  &:hover{
    fill: @basicgreen;
  }
  &:active {
    opacity: 0.5;
  }
}

.social__icon--facebook{
  fill: none;
  stroke-width: 0.9;
    align-self: baseline;
    stroke: @black;
    width: 100%;
}

.social__icon--facebook:hover {
  fill: none;
  stroke: @basicgreen;
}

.social__icon--facebook:hover {
  opacity: 0.5;
}

.social__icon--vkontakte {
  @media (min-width: @tablet) {
    align-self: center;
  }
}
