.button-wrapper{
    margin-top: 16px;
    margin-bottom: 32px;
    padding:0 20px;
    @media (min-width: @tablet) {
      display: flex;
      margin: 0 auto;
      width: 631px;
    }
    @media (min-width: @desktop) {
      width: 1220px;
      padding: 0;

    }
  }
  //small  textare

  .button-wrapper__small {
    margin-top: 23px;
    font-family: Arial, sans-serif;
    font-size: 14px;
    line-height: 16px;
    text-align: center;
    @media (min-width: @tablet) {
      margin-top: 33px;
      font-size: 16px;
      padding-left: 61px;
    }
    @media (min-width: @desktop) {
      padding-left: 81px;
    }
  }
