//Contacts

.contacts-field {
    margin: 0;
    margin-bottom: 12px;
    padding: 0;

    @media (min-width: @tablet) {
      margin-bottom: 46px;
    }
  }



  .contacts-field__wrapper {
    padding: 0 20px;
    position: relative;

    @media (min-width:@tablet) {
      margin: 0 auto;
      width: 630px;
      padding: 0 70px;
    }
    @media (min-width: @desktop) {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      width: 1220px;
      padding: 0;

    }
  }

  .contacts-field__icon {
    fill: none;
    position: absolute;
    &--mail{
      right:11%;
      bottom: 75%;
    }

    &--phone {
      right:10%;
      bottom: 20%;
    }
    @media(min-width:@desktop) {
      &--mail{
        width: 20px;
        height: 16px;
        left:43%;
        bottom: 55%;
      }
      &--phone {
        left:95%;
        bottom: 51%;
      }
    }

  }
