//Buttom

.form-button{
    width: 100%;
    text-align: center;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
    padding-top: 9px;
    padding-bottom: 9px;
    background-color: @basicgreen;
    color: @white;
    outline: none;
    border: none;
    @media (min-width: @tablet) {
      width: 285px;
      font-size: 20px;
      line-height: 20px;
      padding: 16px 0;
      margin-top: 18px;
      margin-bottom: 72px;
    }
    @media (min-width: @desktop) {
      width: 568px;
      margin-bottom: 82px;

    }

    &:hover {
      background-color: @basicgreendark;
    }

    &:active {
      color: fade(@white; 30%);
    }
  }
