.extra-products__wrapper {

  @media (min-width:@tablet) {

  }
  @media (min-width: @desktop) {
    width: 1220px;
    margin: 0 auto;
  }
}

.extra-products__container {
  padding: 0 20px;
  @media (min-width:@tablet) {
    width: 708px;
    padding: 0 30px;
    margin: 0 auto;
  }
  @media (min-width: @desktop) {
    display: flex;
    width: 1220px;
    padding:0;
  }
}

.extra-products__title {
  display: block;
  margin-bottom: 35px;
  margin-top: 28px;
  padding-left: 20px;
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  overflow: hidden;

  &::after {
    content: "";
    display: inline-block;
    width: 100%;
    padding-top: 6px;
    margin-left: 20px;
    border-top: 2px solid @basicgreen;
    margin-right: -100%;

    @media (min-width: @tablet) {
      padding-top: 10px;
    }
  }

  @media (min-width: @tablet) {
    margin: 0 0 81px 0px;
    padding: 0;
    padding-left: 34px;
    font-size: 36px;
    line-height: 36px;

    &::before {
      content: "";
      display: inline-block;
      width: 100%;
      padding-top: 10px;
      margin-right: 34px;
      border-top: 2px solid @basicgreen;
      margin-left: -100%;
    }
  }
    @media (min-width: @desktop) {
    display: inline-block;
    width: 100%;

    &::before {
      overflow: visible;
    }
    }
}

.extra-products__list {
  list-style: none;
  margin: 0;
  padding: 0;

  @media (min-width:@tablet) {
    border: 1px solid @specialgreylight;
    margin-bottom: 65px;
  }

  @media (min-width: @desktop) {
    width: 895px;
    border: none;
  }
}

.extra-products__title-item {
  margin-top: 12px;
  margin-bottom: 13px;
  font-size: 16px;
  line-height: 16px;
  font-weight: 400;
  text-transform: uppercase;
  color: @specialsuperdark;
  @media (min-width: @tablet) {
    margin: 0;
    padding: 0;
    font-size: 20px;
    line-height: 20px;
  }
  @media (min-width: @desktop) {
    &--sugar {
      padding-right: 163px;
    }

    &--water {
      padding-right: 195px;
    }

    &--milk {
      padding-right: 256px;
    }

    &--vitamins {
      padding-right: 234px;
    }
  }
}

.extra-products__item {
  margin-bottom: 18px;
  border-top: 1px solid @specialgreylight;

  @media (min-width: @tablet) {
    padding: 24px 37px 28px 38px;
    margin: 0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    border: none;
    border-bottom: 1px solid @specialgreylight;
  }
  @media (min-width: @desktop) {
    padding: 15px 0 17px;
    margin: 0;
    border-top: 1px solid @specialgreylight;
    border-bottom: 1px solid @specialgreylight;
  }
}


.extra-products__table-item {
  display: flex;
  justify-content: space-between;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 14px;
  color: @specialdarkgrey;

  @media (min-width: @tablet) {
    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: @desktop) {
    width: 284px;
  }
}

.extra-products__amount {
  @media (min-width: @tablet) {
    padding-right: 51px;
  }
  @media (min-width: @desktop) {
    padding: 0;
  }
}
