//Additional

.additional-field {
    margin: 0;
    padding: 0;
    @media (min-width: @desktop) {
      margin-bottom: 68px;
    }
  }

  .additional-field__list {
    padding: 0 20px;
    @media (min-width: @tablet) {
      margin:0 auto;
      width: 631px;
      display: grid;
      grid-template-columns: 343px 287px;
    }
    @media (min-width: @desktop) {
      width: 1220px;
      padding: 0;
      grid-template-columns:221px 221px 221px 221px;
      column-gap: 104px;
    }
  }

  .additional-field__item {
    padding-bottom: 25px;
    padding-left: 45px;
    @media (min-width: @tablet) {
      display: block;
      width: 161px;
      padding-left: 60px;
      padding-bottom: 42px;
    }
    @media (min-width: @desktop) {
      padding:0;
      width: 221px;
      text-align: right;
    }
  }
