.features {
  @media (min-width: @tablet) {
    border-bottom: 1px solid @bordergrey;
  }
  @media (min-width: @desktop) {
    border-bottom: none;
  }
}

.features__wrapper {
  padding: 0 20px;
  margin-bottom: 30px;
  @media (min-width: @tablet) {
    margin: 0 auto;
    width: 708px;
    padding: 0 30px 47px;
  }
  @media (min-width: @desktop) {
    margin: 0 auto;
    width: 1220px;
    padding: 0px 110px 11px;
  }
}

.features__title {
  margin: 0;
  padding-bottom: 38px;
  font-size: 36px;
  line-height: 40px;
  font-weight: 400;
  @media (min-width: @tablet) {
    padding-bottom: 32px;
    font-size: 60px;
    line-height: 60px;
  }
}

.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
  @media (min-width: @tablet) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 137px;
    grid-row-gap: 37px;
    padding-right: 105px;

  }
  @media (min-width: @desktop) {
    grid-template-columns: 230px 246px 248px 250px;
    grid-column-gap: 88px;

    padding: 0;
  }
}

.features__item {
  padding: 0 0 24px 80px;
  position: relative;
  font-size: 14px;
  line-height: 18px;
  font-family: Arial, sans-serif;
  color: @specialdarkgrey;

  @media (min-width: @tablet) {
    padding: 151px 0 57px;
    width: 245px;
    font-size: 16px;
    line-height: 24px;
    z-index: 10;
  }
  @media (min-width: @desktop) {
    padding: 151px 0 57px;
  }
}

.features__icon {
  position: absolute;
  left: 0;
  @media (min-width: @tablet) {
    width: 80px;
    height: 80px;
    top:20px;
   }
}

.features__item--result {
  &::before {
    content: "";
    position: absolute;
    display: block;
    width: 60px;
    height: 60px;
    left: 0;
    background-image: url("../img/svg/Group4.svg");
    @media (min-width: @tablet) {
     width: 80px;
     height: 80px;
     background-repeat: no-repeat;
     background-size: cover;
     top:20px;
    }
}
}

.features__item--preparing {
  &::before {
    content: "";
    position: absolute;
    display: block;
    width: 60px;
    height: 60px;
    left: 0;
    background-image: url("../img/svg/Group2.svg");
    @media (min-width: @tablet) {
      width: 80px;
      height: 80px;
      background-repeat: no-repeat;
      background-size: cover;
      top:20px;
     }
  }
}

.features__item {
  @media (min-width: @tablet) {
    &--healthy::before {
      content: "";
    position: absolute;
    display: block;
    width: 112px;
    height: 280px;
    right: 0;
    top:20px;
    background-image: url("../../img/svg/1.svg");
    background-repeat: no-repeat;
    z-index: -1;
    }
  }
  @media (min-width: @tablet) {
    &--preparing::after {
      content: "";
    position: absolute;
    display: block;
    width: 112px;
    height: 280px;
    right: 0;
    top: 20px;
    background-image: url("../../img/svg/2.svg");
    background-repeat: no-repeat;
    z-index: -1;
    }
  }
  @media (min-width: @tablet) {
    &--meals::before {
      content: "";
    position: absolute;
    display: block;
    width: 112px;
    height: 280px;
    right: 0;
    top:20px;
    background-image: url("../../img/svg/3.svg");
    background-repeat: no-repeat;
    z-index: -1;
    }
  }
  @media (min-width: @tablet) {
    &--result::after {
    content: "";
    position: absolute;
    display: block;
    width: 136px;
    height: 280px;
    right: 0;
    top: 20px;
    background-image: url("../../img/svg/4.svg");
    background-repeat: no-repeat;
    z-index: -1;
    }
  }


}
