.special-card {
  padding: 0 20px;
  padding-top: 77px;
  border-top: 1px solid @specialgreylight;
  border-bottom: 1px solid @specialgreylight;
  position: relative;

  @media (min-width: @tablet) {
    padding: 159px 37px 38px 38px;
    border: 2px solid @basicgrey;
  }
}

.special-card__wrapper {
  display: flex;
  flex-direction: column;
}

.special-card__title {
  width: 280px;
  margin: 0;
  display: inline-block;
  text-align: center;
  align-self: center;
  color: @black;
 
  @media (min-width: @tablet) {
    padding: 0;
    width: 134px;
  }

  @media (min-width: @desktop) {
    margin-left:21px;
  }
}

.special-card__text {
  margin-top: 8px;
  font-family: Arial, sans-serif;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  align-self: center;

  @media (min-width: @tablet) {
    margin-top: 20px;
    width: 167px;
    font-size: 16px;
    line-height: 18px;
  }
  @media (min-width: @desktop){
    width: 167px;
  }
}

.special-card__plus {
  width: 30px;
  height: 30px;
  position: absolute;
  top: 40px;
  left: 45%;

  @media (min-width: @tablet) {
    width: 60px;
    height: 60px;
    left: 40%;
  }
}