.gift {
  margin-top: 55px;
  margin-bottom: 35px;

  @media (min-width: @tablet) {
    padding: 0;
    margin: 0;
    margin-bottom: 76px;
  }
  @media (min-width: @desktop) {
    margin: 0 0 0 80px;
  }
}

.gift__wrapper {
  width: 100%;
  background: @basicgreen;
  background-image: url(../img/gift/gift-photo-mobile\ 1.png);
  background-repeat: no-repeat;
  background-size: contain;
  display: flex;
  flex-direction: column;
  @media (min-resolution: 2dppx) {
    background-image: url("../img/gift/gift-photo-mobile\ 1@2x.png");
    background-size: 100%;
  }
  @media (min-width: @tablet) {
    width: 708px;
    margin: 0 auto;
    background-image: url("../img/gift/gift-photo-tablet\ 1.png");
    @media (min-resolution: 2dppx) {
      background-image: url("../img/gift/gift-photo-tablet\ 1@2x.png");
      background-size: contain;
    }
  }
  @media (min-width: @desktop) {
    width: 245px;
    background-image: url("../img/gift/gift-photo-desktop.png");

    @media (min-resolution: 2dppx) {
      background-image: url("../img/gift/gift-photo-desktop@2x.png");
      background-size: contain;
    }
  }
}

.gift__text {
  padding: 186px 0 43px 0px;
  margin: 0;
  width: 161px;
  position: relative;
  align-self: center;
  font-family: Arial, sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: @white;
  @media (min-width: @tablet) {
    padding: 71px 124px 69px 423px;
  }
  @media (min-width: @desktop) {
    padding: 185px 43px 42px 41px;
  }
  &::after{
    content: "";
    display: block;
    position: absolute;
    background-image: url("../img/svg/icon_gift.svg");
    width: 78px;
    height: 80px;
    top: 20%;
    left: 25%;
    @media (min-width: @tablet) {
    left: 10%;
    top: 30%;
    @media (min-width: @desktop) {
      top: 20%;
      left: 35%;
     }
    }
  }
}
