.button-order {
  margin-top: 8px;
  padding-top: 10px;
  padding-bottom: 10px;
  width: 100%;
  font-family: "Oswald", Arial, sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  background-color: @basicgreen;
  color: @white;
  outline: none;
  border: none;

  &--vitamins {
    border-bottom: 1px solid @specialgreylight;
  }
  &:hover {
    background-color: @basicgreendark;
  }

  &:active {
    color: fade(@white;30%);
  }

    @media (min-width: @tablet) {
      width: 248px;
      margin-top: 20px;
      margin-bottom: 40px;
    }

    @media (min-width: @desktop) {
      width: 168px;
    }
}

.button-order__special-card {
    display: inline-block;
    padding: 20px 0;
    width: 100%;
    font-size: 16px;
    line-height: 0;
    text-transform: uppercase;
    text-align: center;
    text-decoration: none;
    background-color: @basicgrey;
    color: @specialdarkgrey;
    margin-bottom: 24px;

    &:hover {
      background-color: @specialgreylight;
    }

    &:active {
      color: fade(@specialdarkgrey;30%);
    }

    @media (min-width: @tablet) {
        margin-top: 71px;
        margin-bottom: 0;
      }
}


.button-order--extra-products {
  margin-top: 15px;
  @media (min-width: @tablet) {
    width: 632px;
    margin-top: 26px;
    margin-bottom: 0;
  }
  @media (min-width: @desktop) {
    width: 244px;
    margin: 0 0 0 41px;
  }
}
